<template>
    <div class="aox_box">
        <header>
            <van-icon name="search"/>
            <span class="deng" @click="city()">{{ content }}</span>
            <van-icon name="manager-o" @click="my()"/>
        </header>
        <!-- 轮播图 -->
        <div class="swiper">
            <van-swipe class="my-swipe" indicator-color="white">
                <van-swipe-item>
                    <ul class="ul">
                        <li v-for="(item,index) in swiperList" :key="index">
                            <img :src="'https://fuss10.elemecdn.com'+item.image_url" alt="">
                            <p class="ax_p">{{item.title }}</p>
                        </li>
                    </ul>
                </van-swipe-item>
                <van-swipe-item>
                    <ul class="ul">
                        <li v-for="(item,index) in swiperList" :key="index">
                            <img :src="'https://fuss10.elemecdn.com'+item.image_url" alt="">
                            <p class="ax_p">{{item.title }}</p>
                        </li>
                    </ul>
                </van-swipe-item>
            </van-swipe>
        </div>
        <p class="fu_shop">
            <van-icon name="apps-o" />
            <span>附近商家</span>
        </p>
        <!-- 商品列表 -->
        <div class="shop_list">
            <ol>
                <li v-for="(item,index) in shopList" :key="index">
                    <div class="img_list">
                        <img :src="'//elm.cangdu.org/img/'+item.image_path" alt="">
                    </div>
                    <div class="tet">
                        <h4>
                            <span class="ap1">品牌</span>
                            <span class="ap2">{{ item.name }}</span>
                            <span class="ap3">保 准 票</span>
                        </h4>
                        <p class="api_p1">
                            <van-rate v-model="value" />
                            <span class="ap4">{{ item.rating }}</span>
                            <span class="ap5">蜂鸟专送</span>&nbsp;/
                            <span class="ap6">准时送达</span>
                        </p>
                        <p class="api_p2">
                            <span class="ap7">￥{{ item.float_minimum_order_amount }}起送&nbsp;/&nbsp;</span><span class="ap7">配送费约￥{{ item.float_delivery_fee }}</span>
                            <span class="ap9">{{ item.distance }}&nbsp;/&nbsp;</span><span class="ap10">{{ item.order_lead_time }}</span>
                        </p>
                    </div>
                </li>
            </ol>
        </div>
        <footer>
            <van-tabbar route>
                <van-tabbar-item replace icon="location-o">
                    外卖
                </van-tabbar-item>
                <van-tabbar-item replace icon="search">
                    搜索
                </van-tabbar-item>
                <van-tabbar-item replace icon="bar-chart-o">
                    订单
                </van-tabbar-item>
                <van-tabbar-item replace icon="user-circle-o">
                    我的
                </van-tabbar-item>
            </van-tabbar>
        </footer>
    </div>
</template>

<script>
export default {
    data() {
        return {
            content:'',
            swiperList:[],
            shopList:[],
            value:5
        };
    },
    created() {

    },
    mounted() {
        this.ajaximg()
        this.ajaxswiper()
        this.ajaxshop()
    },
    methods: {
        async ajaximg(){
            let { data:res} = await this.$axios.get(`http://elm.cangdu.org/v2/pois/31.273193,121.509774`)
            // console.log(res.name)
            this.content = res.name
        },
        async ajaxswiper(){
            let { data:img } = await this.$axios.get(`http://elm.cangdu.org/v2/index_entry?geohash=45.803133,126.53532&group_type=1&flags[]=F`)
            // console.log(img)
            this.swiperList = img
        },
        async ajaxshop(){
            let { data:shop } = await this.$axios.get(`http://elm.cangdu.org/shopping/restaurants?latitude=45.803133&longitude=126.53532&offset=0&limit=20&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=`)
            console.log(shop)
            this.shopList = shop
        },
        city(){
            this.$router.push('/home')
        },
        my(){
            this.$router.push('/my')
        }
    }
};
</script>

<style lang="scss">
    header{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 500;
    }
    .aox_box{
        width: 100%;
        height: 100%;
        position: relative;
    }
    .swiper{
        width: 100%;
        height: 200px;
    }
    .my-swipe .van-swipe-item {
        color: #fff;
        font-size: 20px;
        height: 200px;
        text-align: center;
        background: #f5f5f5;
    }
    .ul{
        width: 100%;
        height: 200px;
        display: flex;
        align-items: center;
        justify-content: space-around;
        flex-wrap: wrap;
        li{
            width: 25%;
            height: 100px;
            text-align: center;
            img{
                width: 50px;
                height: 50px;
            }
            .ax_p{
                font-size:13px;
                color: #000;
            }
        }     
    }
    .element.style{
        background: crimson;
    }
    .fu_shop{
        width: 100%;
        height: 40px;
        display: flex;
        align-items: center;
        background: #f5f5f5;
        .van-icon{
            margin-left: 10px;
            font-size: 20px;
            color: cadetblue;
        }
        span{
            margin-left: 10px;
            font-size: 14px;
        }
    }
    .shop_list{
        width: 100%;
        ol{
            width: 100%;
            li{
                width: 100%;
                height: 150px;
                display: flex;
                align-items: center;
                box-sizing: border-box;
                .img_list{
                    margin-left: 10px;
                    img{
                        width: 80px;
                        height: 80px;
                    }
                }
                .tet{
                    margin-left: 15px;
                    width: 300px;
                    h4{
                        width: 100%;
                        height: 30px;
                        align-items: center;
                        display: flex;
                        .ap1{
                            width: 40px;
                            height: 30px;
                            display: block;
                            font-size: 14px;
                            background: cornflowerblue;
                            color: #fff;
                            text-align: center;
                            line-height: 30px;
                            border-radius: 5px;
                        }
                        .ap2{
                            width: 160px;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            margin-left: 5px;
                        }
                        .ap3{
                            margin-left: 30px;
                            color: darkgrey;
                            font-size: 12px;
                        }
                    }
                    .api_p1{
                        width: 100%;
                        .van-icon-star::before{
                            color: tomato;
                        }
                        .ap4{
                            font-size: 11px;
                            color: tomato;
                        }
                        .ap5{
                            margin-left: 30px;
                            font-size: 12px;
                            color: turquoise;
                        }
                        .ap6{
                            font-size: 11px;
                            color: darkcyan;                     
                        }
                    }
                    .api_p2{
                        width: 100%;
                        .ap7{
                            font-size: 12px;
                            color: darkgrey;
                        }
                        .ap9{
                            font-size: 13px;
                            color: darkgrey;
                            margin-left: 10px;
                        }
                        .ap10{
                            font-size: 13px;
                            color: dodgerblue;
                        }
                    }
                }
            }
        }
    }
    .van-tabbar-item{
        color: #1D82FE;
    }
</style>
