<template>
    <div class="big">
        <header>
            <van-icon name="arrow-left" @click="fan()"/>
            <span class="deng">密码登录</span>
            <van-icon name="friends-o" />
        </header>
        <div class="box" ref="box">
            <div class="content">
                <div class="login_phone">
                    <p>
                        <input type="text" placeholder="账号" v-model="pho">
                    </p>
                    <p>
                        <input type="password" placeholder="密码"  v-model="pwd">
                    </p>
                    <p>
                        <input type="text" placeholder="验证码" v-model="yan">
                        <img :src="imgList" alt="">
                    </p>
                    <span data-v-ff83a60c="" class="login_tips">
                        温馨提示：未注册过的账号，登录时将自动注册
                    </span>
                    <span data-v-ff83a60c="" class="login_tips">
                        注册过的用户可凭账号密码登录
                    </span>
                    <van-button type="primary" :disabled="btn==false" size="large" @click="dian()">立即登录</van-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import betterscroll from 'better-scroll'
export default {
    components: {
    },
    data() {
        return {
            pho:"",
            pwd:"",
            yan:"",
            imgList:[]
        };
    },
    created() {

    },
    mounted() {
        this.$nextTick(()=>{
            setTimeout(()=>{
                this.bs = new betterscroll(this.$refs.box,{
                    probeType:3
                })
            },500)
        })
        this.$axios.post('http://elm.cangdu.org/v1/captchas').then(({data})=>{
            console.log(data)
            this.imgList = data.code
        })
    },
    methods: {
        fan(){
            this.$router.go(-1)
        },
        setCookie(key,value,t){
            var oDate=new Date();
            oDate.setDate(oDate.getDate()+t);
            document.cookie=key+"="+value+"; expires="+oDate.toDateString();
        },
        //验证正则
        dian(){
            let reg=/^1(3[0-9]|5[0-3,5-9]|7[1-3,5-8]|8[0-9])\d{8}$/;
            if(reg.test(this.pho)&&this.pwd!=""&&this.yan!=""){
                this.$axios.post('http://elm.cangdu.org/v2/login',{"username":this.pho,"password":this.pwd,"captcha_code":this.yan}).then((res)=>{
                    console.log(res)
                    if(res.statusText=="OK"){
                        this.$router.push('/home')
                    }else{
                       alert('失败，请重新登录')
                    }
                })
            }
        }
    },
    computed: {
        btn(){
            return this.pho!==""&&this.pwd!==""&&this.yan!==""
        }
    }
};
</script>

<style lang="scss">
    .login_tips[data-v-ff83a60c] {
        font-size: .5rem;
        color: red;
        padding: .4rem .6rem;
        line-height: 30px;
        display: block;
        height: 30px;
    }
    .big{
        width: 100%;
        height: 100%;
    }
    header{
        width: 100%;
        .van-icon{
            font-size: 30px;
        }
        .deng{
            font-size: 16px;
        }
    }
    .login_phone{
        width: 100%;
        height: 400px;
        display: flex;
        flex-direction: column;
        margin-top: 50px;
        p{
            width: 100%;
            height: 50px;
            display: felx;
            align-items: center;
            justify-content: space-around;
            margin-bottom: 5px;
            margin-top: 10px;
            input{
                width: 300px;
                height: 40px;
                margin-top: 10px;
                border: 0;
                margin-left: 10px;
                text-indent: 10px;
            }
            input::placeholder{
                    color: darkgrey;
            }
            .van-switch{
                margin-top: 10px;
            }
            img{
                width: 60px;
                height: 20px;
            }
        }
    }
    .box{
        width: 100%;
        height: 100%;
        overflow: hidden;
    }
    .content{
        flex: 1;
        min-height: 101%;
    }
</style>
