<template>
    <div class="big_box">
        <header>
            <van-icon name="arrow-left" @click="fan()"/>
            <span class="deng">{{ add }}</span>
            <i class="el-icon-platform-eleme"></i>
        </header>
        <!-- <div class="box" ref="box"> -->
            <div class="content">
               <div class="search">
                   <van-search v-model="value" placeholder="请输入搜索关键词" @keyup.enter="ass"/>
                   <van-button type="info" @click="ajaxsearch()">提交</van-button>
               </div>
               <div class="newlist">
                   <ol>
                       <li v-for="(item,index) in arr" :key="index">
                           <p class="item_p1" @click="ajaxdetail(item.name)">{{ item.name }}</p>
                           <p class="item_p2">{{ item.address }}</p>
                       </li>
                   </ol>
               </div>
            </div>
        <!-- </div> -->
    </div>
</template>

<script>
// import better from 'better-scroll'
export default {
    data() {
        return {
            add:'',
            value:"",
            arr:[]
        };
    },
    created() {
        
    },
    mounted() {
        // this.$nextTick(()=>{
        //     setTimeout(()=>{
        //         this.bs = new better(this.$refs.box,{
        //             probeType:3
        //         })
        //     },500)
        // })
        // this.ajaxsearch()
        this.add = this.$route.query.data.name
    },
    methods: {
        fan(){
            this.$router.go(-1)
        },
        async ajaxsearch(){
            let { data:res } = await this.$axios.get(`http://elm.cangdu.org/v1/pois?type=search&city_id=${this.$route.query.data.id}&keyword=${this.value}`)
            console.log(res)
            this.arr = res
        },
        async ajaxdetail(i){
            console.log(i)
            this.$router.push({
                path:"/detail"
            })
        }
    }
};
</script>

<style lang="scss">
    .big_box{
        width: 100%;
        background: #f5f5f5;
    }
    // .box{
    //     width: 100%;
    //     height: 100%;
    //     overflow: hidden;
    // }
    .content{
        flex: 1;
        overflow: auto;
    }
    .el-icon-platform-eleme{
        font-size: 25px;
    }
    .search{
        width: 100%;
        height: 130px;
        display: flex;
        margin-top: 10px;
        flex-direction: column;
        align-items: center;
        background: #fff;
        margin-top: 50px;
        .van-search{
            width: 95%;
            height: 60px;
        }
        .van-button{
            width: 90%;
            margin-top: 10px;
        }
    }
    .newlist{
        width: 100%;
        ol{
            width: 100%;
            li{
                width: 100%;
                height: 50px;
                display: flex;
                flex-direction: column;
                box-sizing: border-box;
                border-bottom: darkgrey 1px solid;
                .item_p1{
                    margin-left: 30px;
                    margin-top: 3px;
                    color: dodgerblue;
                }
                .item_p2{
                    margin-left: 30px;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    font-size: 13px;
                    color: darkgrey;
                }
            }
        }
    }
    header{
    width: 100%;
    height: 50px;
    background: #3190E8;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 0 10px;
    flex-grow: 0;
  }
</style>
