<template>
  <div id="app">
    <header>
      <span>ele.cm</span>
      <div v-show="!flag"></div>
      <van-icon v-show="flag" name="back-top" @click="toJump('zd')"></van-icon>
      <van-icon name="user-circle-o" @click="tiao()" />
    </header>
    <ul class="uli">
        <li v-for="(item,index) in list" :key="index">
          <span @click="toJump(item)">{{ item }}</span>
        </li>
    </ul>
    <!-- <div class="box"> -->
      <!-- <div class="box"> -->
      <div class="content">
        <!-- 定位当前位置 -->
         <div class="nav_top">
           <p class="p1">
             <span class="sp1">当前定位的城市：</span>
             <span class="sp2">定位不准时，请在当前列表中选择</span>
           </p>
           <p class="p2">
             <span class="bei" @click="ass()">
                {{ nameList.name }}
             </span>
             <van-icon name="arrow" class="dian"/>
           </p>
         </div>
         <p class="p3"></p>
         
         <!-- 热门城市 -->
         <div class="hot_city">
           <p class="hot_re">热门城市</p>
           <ul class="ul">
             <li v-for="(item,index) in hotList" :key="index" @click="ass(item.id)">
                {{ item.name }}
             </li>
           </ul>
         </div>
         <!-- 数据 -->
         <div class="all_city" v-for="(item,index) in list" :key="index">
           <p class="yt_index" :id="item" :ref="item">{{ item }}</p>
            <ol v-for="(ite,ind) in cityList[item]" :key="ind">
              <li>
               <!-- <span @click="ajaxtiao(ite.id)">{{ ite.name }}</span>  -->
               <span @click="ass(ite.id)">{{ ite.name }}</span>
              </li>
            </ol>
         </div>
        <router-view/>
      </div>
    <!-- </div> -->
  </div>
  <!-- </div> -->
</template>
<script>
// import Betterscroll from 'better-scroll'
export default {
  data () {
    return {
      nameList:[],
      hotList:[],
      cityList:{},
      list:[],
      flag:false
    }
  },
  // components: {
  //     Betterscroll
  // },
  created () {
      
  },
  mounted () {
    this.ajaxname()
    this.hotlist()
    this.ajaxcity()
    window.addEventListener('scroll', this.handleScrollx, true)
    // this.$nextTick(()=>{
    //   setTimeout(()=>{
    //     this.bs = new Betterscroll(this.$refs.scrollBox,{
    //       probeType:3
    //     })
    //   },500)
    // })
  },
  methods: {
      async ajaxname(){
        let {data:res} = await this.$axios.get('http://elm.cangdu.org/v1/cities?type=guess')
        // console.log(res)
        this.nameList = res
      },
      async hotlist(){
        let {data:arr} = await this.$axios.get('http://elm.cangdu.org/v1/cities?type=hot')
        // console.log(arr)
        this.hotList = arr
      },
      async ajaxcity(){
        let { data:city } = await this.$axios.get('http://elm.cangdu.org/v1/cities?type=group')
        // console.log(city)
        this.cityList = city
        // console.log(Object.keys(city))

        this.list = Object.keys(city).sort()
        this.$set(this.list)
        // console.log(this.list)
      },
      async ass(i){
          console.log(i)
          let {data} = await this.$axios.get(`http://elm.cangdu.org/v1/cities/${i}`)
          console.log(data)
          this.$router.push({
          path:'/list',
          query:{
            data
          }
        })
      },
      tiao(){
        this.$router.push('/login')
      },
      // 跳转到指定位置
      toJump(z) {
        if(z != "zd"){ // 点击的是字母
          document.body.scrollTop = document.documentElement.scrollTop = this.$refs[z][0].offsetTop - 50;
        } else {// 吸顶
          document.body.scrollTop = document.documentElement.scrollTop = 0
        }
        // console.log(this.$refs[z][0].offsetTop - 50)  // 获取元素距离顶部的高度 减去50 
      },
      handleScrollx() {
        // console.log('滚动高度', window.pageYOffset)
        if(window.pageYOffset>50) {
          this.flag = true
        } else {
          this.flag = false
        }
    },
  }
}
</script>
<style lang="scss" scoped>
  #app{
    width: 100%;
    height: 100%;
    position: relative;
  }
  .uli{
    width: 10px;
    display: flex;
    flex-direction: column;
    position: fixed;
    right: 10px;
    top: 100px;
    z-index: 1000;
    li{
      width: 100%;
      span{
        width: 20px;
        display: block;
        margin-top: 5px;
        color: #3190E8;
      }
    }
  }
  // .box{
  //   width: 100%;
  //   height: 100%;
  //   overflow: hidden;
  // }
  .all_city{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    ol{
      width: 25%;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      li{
        width: 100%;
        line-height: 30px;
        height: 30px;
        text-align: center;
        border: #E4E4E4 1px solid;
        border-right: 0;
        font-size: 13px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }
  .yt_index{
    width: 100%;
    height: 35px;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    padding: 0 10px;
    color: #39a9ed;
    font-size: 14px;
    background: #F5F5F5;
  }
  .hot_city{
    width: 100%;
    height: 115px;
    background: #f5f5f5;
    p{
      width: 100%;
      height: 30px;
      border-bottom: darkgray 1px solid;
      font-size: 13px;
      display: flex;
      align-items: center;
      box-sizing: border-box;
      padding: 0 10px;
      color: #888;
    }
    ul{
      width: 100%;
      height: 80px;
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      border-collapse: collapse;
      li{
        width: 24.7%;
        height: 40px;
        text-align: center;
        line-height: 40px;
        border: #E4E4E4 1px solid;
        border-right: 0;
        color: #3190E8;
        font-size: 13px;
      }
    }
  }
  .p3{
    width: 100%;
    height: 10px;
    background: #F5F5F5;
    border-bottom: darkgray 1px solid;
  }
  .nav_top{
    width: 100%;
    height: 90px;
  }
  .nav_top>.p1{
      width: 100%;
      height: 50%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      box-sizing: border-box;
      padding: 0 10px;
      border-bottom: darkgray 1px solid;
  }
  .p2{
    width: 100%;
    height: 50%;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    padding: 0 10px;
    justify-content: space-between;
    border-bottom: darkgray 1px solid;
    .bei{
      font-size: 18px;
      color: #3190E8;
    }
    .dian{
      font-size: 30px;
      color: darkgrey;
    }
  }
  .sp1{
      font-size: 13px;
      color: #888;
      display: block;
  }
  .sp2{
    font-size: 12px;
    font-weight: 600;
    color: darkgray;
    display: block;
  }
  .van-icon-location-o::before {
    font-size: 25px;
  }
.my-swipe .van-swipe-item {
    color: #fff;
    font-size: 20px;
    height: 200px;
    text-align: center;
    background-color: #39a9ed;
  }
  img{
    width: 100%;
    height: 100%;
  }
  *{
    margin: 0;
    padding: 0;
    list-style: none;
  }
  html,body,#app{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
  header{
    width: 100%;
    height: 50px;
    background: #3190E8;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 0 10px;
    flex-grow: 0;
  }
  footer{
    width: 100%;
    height: 50px;
    background: cadetblue;
    color: #fff;
  }
  // .box{
  //   width: 100%;
  //   height: 100%;
  //   overflow: hidden;
  // }
  .content{
    flex: 1;
    // min-height: 101%;
    overflow: auto;
  }
  
</style>
